.scrollstop {
  height: 100%;
  min-height: 100%;
  overflow: hidden !important;
  touch-action: none;
}
@media all and (min-width:320px) {
  .modal {
    position: fixed;
    top: 0;
    width: 100vw;
    height: 100vh;
    background-color: rgba(3, 3, 3, 0.7);
    opacity: 0;
    transition: all 1s;
    
  }
  .modal.active {
    opacity: 1;
    z-index: 9999;
  }
  .modal .modal-box {
    position: relative;
    top: 50%;
    left: 50%;
    transform: translateY(-50%) translateX(-50%);
    width: 80%;
    background-color: #fff;
  }
  .modal .modal-box .modal-header {
    text-align: right;
  }
  .modal .modal-box .modal-header .material-icons {
    padding: 10px;
    font-weight: 800;
    font-size: 34px;
    cursor: pointer;
  }
  .modal .modal-box .modal-body {
    height: 50vh;
  }
  .modal .modal-box .modal-body ul li {
    padding: 20px;
    text-align: center;
  }
  .modal .modal-box .modal-body ul li a {
    position: relative;
    text-transform: uppercase;
    font-weight: 600;
  }
  .modal .modal-box .modal-body ul li a::after {
    content: "";
    position: absolute;
    left: 0;
    display: block;
    width: 0;
    height: 1px;
    background-color: #333;
    transition: width 0.3s;
  }
  .modal .modal-box .modal-body ul li a:hover::after {
    width: 100%;
    left: 0;
  }
}